import React, { memo, useEffect, useState } from "react";
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
// library estefade shode baray custom scrollbar
import "react-perfect-scrollbar/dist/css/styles.css";
// library estefadeh shode baray link dadan var routing
import { NavLink } from "react-router-dom";
// file jquery
import $ from "jquery";
// library estefadeh shode baraye translate
import { useTranslation } from "react-i18next";

import "nouislider/distribute/nouislider.css";
//materilize library css baray estefade az componenthai materilize
import M from "materialize-css";
// functional componenthai react redux baray dispatch kardan actionha
import { useDispatch, useSelector } from "react-redux";
// action hai sakhteh shode , fatakhani jahat estefade
import {
  Get_all_doctors,
  Get_one_doctor,
  Update_Doctor,
  Delete_one_doctor,
} from "../../../../entity/Redux/Admin/Action";

// materila ui style baray sakht reange
import { makeStyles } from "@material-ui/core/styles";
// baray esefade az typography material ui
import Typography from "@material-ui/core/Typography";
// slider range material ui baray show commision doctor
import Slider from "@material-ui/core/Slider";

// instyle baray range comission
const useStyles = makeStyles((theme) => ({
  root: {
    width: 300,
  },
  margin: {
    height: theme.spacing(3),
  },
}));

// markha baray point default reange comission as
const marks = [
  {
    value: 0,
    label: "0",
  },
  {
    value: 10,
    label: "10",
  },
  {
    value: 20,
    label: "20",
  },
  {
    value: 30,
    label: "30",
  },
  {
    value: 40,
    label: "40",
  },
  {
    value: 50,
    label: "50",
  },
  {
    value: 60,
    label: "60",
  },
  {
    value: 70,
    label: "70",
  },
  {
    value: 80,
    label: "80",
  },
  {
    value: 90,
    label: "90",
  },
  {
    value: 100,
    label: "100",
  },
];

// functional component asli safhe singin
export default memo(() => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { t, i18n } = useTranslation();
  const [data, setData] = useState({});
  const [updateData, setUpdateData] = useState();

  // in function bad az ejraie component ejra mishavad
  useEffect(() => {
    // codehai javascript materilize++
    //code materilize baray zoom tasvir
    var materilbox = document.querySelectorAll(".materialboxed");
    M.Materialbox.init(materilbox);
    // code materilize baray select options
    var elems = document.querySelectorAll("select");
    M.FormSelect.init(elems);

    // dispatch kardan action get_all_doctors() baray daryaft
    dispatch(Get_all_doctors());
  }, []);

  // namayesh range commision zaman swip kardan
  function valuetext(value) {
    return `${value}Â°C`;
  }

  {
    /*
        -- state get
        -- greftan stateha az reducer
    */
  }
  const AdminDoctors = useSelector((state) => state.Admin);

  // end state get
  {
    /*
        -- delete doctor
        -- hazf doctor ba id
    */
  }

  function deleteDoctor(id) {
    dispatch(Delete_one_doctor(id));
    // console.log("delete : " , id)
  }

  // end delete

  console.log("dataaaaa : ", AdminDoctors);

  if (AdminDoctors.deleteDoctor) {
    if (AdminDoctors.deleteDoctor.status === 200) {
      M.toast({ html: "The removal was successful ..." });
      setTimeout(() => {
        window.location.pathname = "/doctors-list";
      }, 1500);
    } else {
      M.Toast({ html: "please try later ..." });
    }
  }

  function handleLang(e) {
    e.preventDefault();
    if (!localStorage.getItem("lan")) {
      localStorage.setItem("lan", "en");
    }

    var lan = localStorage.getItem("lan");
    var ckeckbox = $("input[name=cahngelan]");
    if (ckeckbox.is(":checked") && lan === "en") {
      localStorage.setItem("lan", "ch");
    } else {
      localStorage.setItem("lan", "en");
    }

    let lang = localStorage.getItem("lan");
    i18n.changeLanguage(lang);
  }

  function handleEditDoctor(id) {
    setUpdateData();
    dispatch(Get_one_doctor(id));
  }

  const HandleUpdateData = (e) => {
    setUpdateData({
      ...updateData,
      [e.target.name]: e.target.value,
    });
  };

  function submitFormUpdate(e){
    e.preventDefault();
    let id =AdminDoctors.oneDoctor.data.data.id;
    if(updateData)
    {
      dispatch(Update_Doctor(id ,updateData));
    }
  }

  console.log("update data : ", updateData);

  const checked = "checked";
  //   end language
  return (
    <div>
      <div id="loader-wrapper">
        <div id="loader"></div>
        <div className="loader-section section-left"></div>
        <div className="loader-section section-right"></div>
      </div>

      <Header />
      <div id="main">
        <div className="wrapper">
          <SideBar />

          <div id="back_shado"></div>
          <div className="container">
            <div className="row">
              <div className="col s12">
                <div id="breadcrumbs-wrapper">
                  <div className="container">
                    <div className="row">
                      <div className="col s10 m6 l6">
                        <h5 className="breadcrumbs-title">Doctors list</h5>
                        <ol className="breadcrumbs">
                          <li>
                            <NavLink to="/admin">
                              <a>Dashboard</a>
                            </NavLink>
                          </li>
                          <li>
                            <a className="active">Doctors list</a>
                          </li>
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/*

                            */}
              <div className="col s12" style={{ marginBottom: 30 }}>
                <div
                  className="chat_box"
                  style={{ height: "auto", background: "none" }}
                >
                  <div id="work-collections">
                    <div className="row">
                      <div className="col s12 m12 l6">
                        <ul
                          id="projects-collection"
                          className="collection z-depth-1"
                        >
                          <li className="collection-item avatar">
                            <img
                              src="img/icons/doctorB.png"
                              style={{
                                position: "absolute",
                                width: 40,
                                left: 15,
                              }}
                            />
                            <h6 className="collection-header m-0">Doctor(s)</h6>
                            <p>List of All doctors</p>
                          </li>

                          <table
                            className={"striped"}
                            style={{ background: "#222222" }}
                          >
                            <thead>
                              <tr>
                                <th>Full Name</th>
                                <th>Actios</th>
                              </tr>
                            </thead>
                            <tbody>
                              {AdminDoctors.doctors ? (
                                AdminDoctors.doctors.map((item) => {
                                  return (
                                    <tr>
                                      <td>{item.fullName}</td>
                                      <td id={"action-admin-doctor"}>
                                        <i
                                          className="material-icons"
                                          onClick={(e) => deleteDoctor(item.id)}
                                          style={{ cursor: "pointer" }}
                                        >
                                          delete
                                        </i>

                                        <i
                                          className="material-icons"
                                          onClick={(e) =>
                                            handleEditDoctor(item.id)
                                          }
                                          style={{ cursor: "pointer" }}
                                        >
                                          edit
                                        </i>
                                      </td>
                                    </tr>
                                  );
                                })
                              ) : (
                                <p>please wait ...</p>
                              )}
                            </tbody>
                          </table>
                        </ul>
                      </div>

                      <div className="col s12 m12 l6">
                        <ul
                          id="projects-collection"
                          className="collection z-depth-1"
                        >
                          <li className="collection-item avatar">
                            <i className="material-icons" id="custom_icon">
                              edit
                            </i>
                            <h6 className="collection-header m-0">Edit</h6>
                            <p>Edit doctor's data</p>
                          </li>
                          {AdminDoctors.oneDoctor.data ? (
                            <div
                              className={"container"}
                              id={"edit-doctor-main"}
                            >
                              <div
                                className="input-field col s12"
                                style={{ marginTop: 0 }}
                              >
                                <p style={{ color: "#ccc" }}>
                                  &nbsp;&nbsp;fullName
                                </p>
                                <div id="input_back">
                                  <input
                                    autoComplete="false"
                                    name="fullName"
                                    onChange={(e) => HandleUpdateData(e)}
                                    placeholder = {
                                      AdminDoctors.oneDoctor.data.data.fullName
                                    }
                                    type="text"
                                    className="validate"
                                  />
                                </div>
                                {/*<label htmlFor="password">Password</label>*/}
                              </div>

                              <div
                                className="col s12 l6"
                                style={{ marginBottom: 20, paddingLeft: 27 }}
                              >
                                <p>
                                  <input
                                    name="Assistance"
                                    checked={
                                      AdminDoctors.oneDoctor.data.data
                                        .isAssistant
                                    }
                                    onChange={(e) => HandleUpdateData(e)}
                                    id="Assistance"
                                    className="with-gap"
                                    type="checkbox"
                                  />
                                  <label htmlFor="Assistance">
                                    isAssistance
                                  </label>
                                </p>
                              </div>

                              <div
                                className={"col s12"}
                                style={{ marginBottom: "35px" }}
                              >
                                <ul className={"dox-ul"}>
                                  {AdminDoctors.oneDoctor.data.data.attachment.map(
                                    (items) => {
                                      var type_file = items.split(".");
                                      var typeLower = type_file[
                                        type_file.length - 1
                                      ].toLowerCase();
                                      if (
                                        typeLower === "png" ||
                                        typeLower === "jpeg" ||
                                        typeLower === "jpg"
                                      ) {
                                        return (
                                          <li>
                                            <div className={"box-doxs imgs"}>
                                              <span className={"close"}>
                                                <i className={"material-icons"}>
                                                  close
                                                </i>
                                              </span>
                                              <img
                                                src={items}
                                                className={
                                                  "responsive-img materialboxed"
                                                }
                                                width={50}
                                              />
                                            </div>
                                          </li>
                                        );
                                      } else {
                                        return (
                                          <li>
                                            <div className={"box-doxs"}>
                                              <span className={"close"}>
                                                <i className={"material-icons"}>
                                                  close
                                                </i>
                                              </span>
                                              <a
                                                target={"_blank"}
                                                style={{ color: "#fff" }}
                                                href={items}
                                              >
                                                <i className={"material-icons"}>
                                                  insert_drive_file
                                                </i>
                                              </a>
                                            </div>
                                          </li>
                                        );
                                      }
                                    }
                                  )}
                                </ul>
                              </div>

                              {!AdminDoctors.oneDoctor.data.data.isAssistant ? (
                                <div>
                                  <div className="col s12 l6">
                                    
                                    <select className="browser-default" name="educationDegree" onChange={(e) => HandleUpdateData(e)}>
                                      <option
                                        value="0"
                                        disabled
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationDegree == ""
                                            ? true
                                            : false
                                        }
                                      >
                                        Doctor Education Degre
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationDegree == "Diploma"
                                            ? true
                                            : false
                                        }
                                        value="Diploma"
                                      >
                                        Diploma
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationDegree == "Masters"
                                            ? true
                                            : false
                                        }
                                        value="Masters"
                                      >
                                        Masters
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationDegree == "Phd"
                                            ? true
                                            : false
                                        }
                                        value="Phd"
                                      >
                                        PHD
                                      </option>
                                    </select>
                                    <label>Education Degre</label>
                                  </div>

                                  <div className="col s12 l6">
                                    <select className="browser-default" name="educationField" onChange={(e) => HandleUpdateData(e)}>
                                      <option
                                        value="0"
                                        disabled
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationField == ""
                                            ? true
                                            : false
                                        }
                                      >
                                        Doctor Education Degre
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationField == "kids"
                                            ? true
                                            : false
                                        }
                                        value="Psychology"
                                      >
                                        Kids
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationField == "Masters"
                                            ? true
                                            : false
                                        }
                                        value="Psychology"
                                      >
                                        Psychology
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .educationField == "Phd"
                                            ? true
                                            : false
                                        }
                                        value="Psychology"
                                      >
                                        Psychology
                                      </option>
                                    </select>
                                    <label>Education Field</label>
                                  </div>

                                  <div
                                    className="input-field col s12"
                                    style={{ marginTop: 0 }}
                                  >
                                    <p>
                                      <span
                                        style={{
                                          float: "left",
                                          marginTop: "12px",
                                        }}
                                      >
                                        Assistant :{" "}
                                      </span>
                                      <input
                                      onChange={(e) => HandleUpdateData(e)}
                                        name="needAssistant"
                                        defaultChecked={
                                          AdminDoctors.oneDoctor.data.data
                                            .needAssistant == true
                                            ? true
                                            : false
                                        }
                                        style={{ width: 160, height: 30 }}
                                        id="needass"
                                        className="with-gap"
                                        defaultValue={"true"}
                                        type="radio"
                                      />
                                      <label htmlFor="needass">
                                        Needs an assistant
                                      </label>
                                      &nbsp;&nbsp;&nbsp;
                                      <input
                                      onChange={(e) => HandleUpdateData(e)}
                                        name="needAssistant"
                                        className="with-gap"
                                        defaultValue={"false"}
                                        defaultChecked={
                                          AdminDoctors.oneDoctor.data.data
                                            .needAssistant == false
                                            ? true
                                            : false
                                        }
                                        style={{ width: 160, height: 30 }}
                                        id="hasass"
                                        type="radio"
                                      />
                                      <label htmlFor="hasass">
                                        Has an assistant
                                      </label>
                                    </p>
                                  </div>

                                 
                                  <div
                                    className="input-field col s12"
                                    style={{ marginTop: 0 }}
                                  >
                                    <p>
                                      <span
                                        style={{
                                          float: "left",
                                          marginTop: "12px",
                                        }}
                                      >
                                        doctor status :
                                      </span>
                                      <input
                                        name="isConfirmedByAdmin"
                                        onChange={(e) => HandleUpdateData(e)}
                                        style={{ width: 85, height: 30 }}
                                        defaultChecked={
                                          AdminDoctors.oneDoctor.data.data
                                            .isConfirmedByAdmin == true
                                            ? true
                                            : false
                                        }
                                        id="enabled"
                                        className="with-gap"
                                        value="true"
                                        type="radio"
                                      />
                                      <label htmlFor="enabled">enabled</label>
                                      &nbsp;&nbsp;&nbsp;
                                      <input
                                        name="isConfirmedByAdmin"
                                        onChange={(e) => HandleUpdateData(e)}
                                        className="with-gap"
                                        defaultValue="false"
                                        defaultChecked={
                                          AdminDoctors.oneDoctor.data.data
                                            .isConfirmedByAdmin == false
                                            ? true
                                            : false
                                        }
                                        style={{ width: 85, height: 30 }}
                                        id="disabled"
                                        type="radio"
                                      />
                                      <label htmlFor="disabled">disabled</label>
                                    </p>
                                  </div>

                                  <div
                                    className={"col s12"}
                                    style={{ margin: "15px 0", padding: 0 }}
                                  >
                                    <div
                                      className={"col s12 l6"}
                                      style={{ marginTop: "10px" }}
                                    >
                                      <p>Commision rate</p>
                                      <div id="input_back">
                                        <input
                                        onChange={(e) => HandleUpdateData(e)}
                                          placeholder={
                                            AdminDoctors.oneDoctor.data.data
                                              .commissionRate
                                          }
                                          autoComplete="false"
                                          name="commissionRate"
                                          type="text"
                                          className="validate"
                                        />
                                      </div>
                                    </div>
                                  </div>

                               


                                  <div className="col s12 l6">
                                    <select className="browser-default" name="genderType" onChange={(e) => HandleUpdateData(e)}>
                                      <option
                                        value="0"
                                        disabled
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .genderType == ""
                                            ? true
                                            : false
                                        }
                                      >
                                        Doctor Gender Type
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .genderType == "male"
                                            ? true
                                            : false
                                        }
                                        value="male"
                                      >
                                        Male
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .genderType == "female"
                                            ? true
                                            : false
                                        }
                                        value="female"
                                      >
                                        Female
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .genderType == "other"
                                            ? true
                                            : false
                                        }
                                        value="other"
                                      >
                                        Other
                                      </option>
                                    </select>
                                    <label>Doctor Gender</label>
                                  </div>




                                  <div className="col s12 l6">
                                    <select className="browser-default" name="grade" onChange={(e) => HandleUpdateData(e)}>
                                      <option
                                        value="0"
                                        disabled
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .grade == ""
                                            ? true
                                            : false
                                        }
                                      >
                                        Doctor Grade
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .grade == 2
                                            ? true
                                            : false
                                        }
                                        value="2"
                                      >
                                        3
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .grade == 5
                                            ? true
                                            : false
                                        }
                                        value="5"
                                      >
                                        5
                                      </option>
                                      <option
                                        selected={
                                          AdminDoctors.oneDoctor.data.data
                                            .grade == 7
                                            ? true
                                            : false
                                        }
                                        value="7"
                                      >
                                        7
                                      </option>
                                    </select>
                                    <label>Doctor Grade</label>
                                  </div>



                                </div>
                              ) : (
                                ""
                              )}

                              <div
                                className="col s12 m4 l3"
                                id={"action-com"}
                                style={{ marginTop: 0 }}
                              >
                                <a
                                onClick={e => submitFormUpdate(e)}
                                  className={"btn waves-effect blue darken-4"}
                                  style={{ textTransform: "unset" }}
                                >
                                  Update
                                </a>
                              </div>
                            </div>
                          ) : (
                            <p id={"wait-p"}>
                              select doctor for edit
                              <span></span>
                            </p>
                          )}
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
